  <d2-container>
    <template slot="header">
      <instence-select :isupf="false" @changeInstence="changeInstance"></instence-select>
    </template>

    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="12">
          <el-form label-position="left" class="demo-table-expand" size="mini">
            <el-form-item label="N9A接口接收GTP包数"
              ><span>{{ serverData.upf_n9a_inc_pkt }}</span></el-form-item
            >
            <el-form-item label="N9A接口发送GTP包数"
              ><span>{{ serverData.upf_n9a_og_pkt }}</span></el-form-item
            >
            <el-form-item label="N9A接口接收GTP包字节数"
              ><span>{{ serverData.upf_n9a_inc_oct }}</span></el-form-item
            >
            <el-form-item label="N9A接口发送GTP包字节数"
              ><span>{{ serverData.upf_n9a_og_oct }}</span></el-form-item
            >
            <el-form-item label="N9A接口接收错误GTP包数"
              ><span>{{ serverData.upf_n9a_disc_pkt }}</span></el-form-item
            >
          </el-form>
        </el-col>
        <el-col :span="12">
          <el-form label-position="left" class="demo-table-expand" size="mini">
            <el-form-item label="N9C接口接收GTP包数"
              ><span>{{ serverData.upf_n9c_inc_pkt }}</span></el-form-item
            >
            <el-form-item label="N9C接口发送GTP包数"
              ><span>{{ serverData.upf_n9c_og_pkt }}</span></el-form-item
            >
            <el-form-item label="N9C接口接收GTP包字节数"
              ><span>{{ serverData.upf_n9c_inc_oct }}</span></el-form-item
            >
            <el-form-item label="N9C接口发送GTP包字节数"
              ><span>{{ serverData.upf_n9c_og_oct }}</span></el-form-item
            >
            <el-form-item label="N9C接口接收错误GTP包数"
              ><span>{{ serverData.upf_n9c_disc_pkt }}</span></el-form-item
            >
          </el-form>
        </el-col>
      </el-row>
    </el-card>

    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <el-tab-pane
        v-for="(item, index) in tabPaneList"
        :key="index"
        :label="item.tabLabel"
        :name="item.tabLabel"
      >
        <el-table :data="serverData.n9aIncPktDnnList" style="width: 100%">
          <!-- <el-table-column prop="id" label="ID" width="180">
          </el-table-column> -->
          <el-table-column prop="dnn_name" label="DN_Name"> </el-table-column>
          <el-table-column prop="n9a_inc_pkt" label="N9A接收GTP包数">
          </el-table-column>
          <el-table-column prop="n9a_og_pkt" label="N9A发送GTP包数">
          </el-table-column>
          <el-table-column prop="n9a_inc_oct" label="N9A接收GTP包字节数">
          </el-table-column>
          <el-table-column prop="n9a_og_oct" label="N9A发送GTP包字节数">
          </el-table-column>
          <el-table-column prop="n9a_disc_pkt" label="N9A接收错误GTP包数">
          </el-table-column>

          <el-table-column prop="n9c_inc_pkt" label="N9C接收GTP包数">
          </el-table-column>
          <el-table-column prop="n9c_og_pkt" label="N9C发送GTP包数">
          </el-table-column>
          <el-table-column prop="n9c_inc_oct" label="N9C接收GTP包字节数">
          </el-table-column>
          <el-table-column prop="n9c_og_oct" label="N9C发送GTP包字节数">
          </el-table-column>
          <el-table-column prop="n9c_disc_pkt" label="N9C接收错误GTP包数">
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
  </d2-container>
</template>

<script>
import {
  fetchGetPerformN9,
} from "@/api/demo/index";
import instenceSelect from '../compoents/instenceSelect.vue';
export default {
  components: { instenceSelect },
  name: "performN9",
  data() {
    return {
      activeName: "分DN统计",
      instanceValue: "", //当前选中的实例
      isFirst: true,
      loading: false,
      serverData: {},
      tabPaneList: [
        { tabLabel: "分DN统计" },
        // { tabLabel:'分DNN的N9A接口接收GTP包数', dataKey: 'n9aIncPktDnnList', columKey: 'n9a_inc_pkt' },
        // { tabLabel:'分DNN的N9A接口发送GTP包数', dataKey: 'n9aOgPktDnnList', columKey: 'n9a_og_pkt' },
        // { tabLabel:'分DNN的N9A接口接收GTP包字节数', dataKey: 'n9aIncOctDnnList', columKey: 'n9a_inc_oct' },
        // { tabLabel:'分DNN的N9A接口发送GTP包字节数', dataKey: 'n9aOgOctDnnList', columKey: 'n9a_og_oct' },
        // { tabLabel:'分DNN的N9A接口接收错误GTP包数', dataKey: 'n9aDiscPktDnnList', columKey: 'n9a_disc_pkt' },

        // { tabLabel:'分DNN的N9C接口接收GTP包数', dataKey: 'n9cIncPktDnnList', columKey: 'n9c_inc_pkt' },
        // { tabLabel:'分DNN的N9C接口发送GTP包数', dataKey: 'n9cOgPktDnnList', columKey: 'n9c_og_pkt' },
        // { tabLabel:'分DNN的N9C接口接收GTP包字节数', dataKey: 'n9cIncOctDnnList', columKey: 'n9c_inc_oct' },
        // { tabLabel:'分DNN的N9C接口发送GTP包字节数', dataKey: 'n9cOgOctDnnList', columKey: 'n9c_og_oct' },
        // { tabLabel:'分DNN的N9C接口接收错误GTP包数', dataKey: 'n9cDiscPktDnnList', columKey: 'n9c_disc_pkt' }
      ],
    };
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
    // 选择实例
    changeInstance(val) {
      // 图表置空
      this.serverData = {};
      this.instanceValue = val
      // 获取告警数据
      this.getData();
    },
    // 获取数据
    getData() {
      this.loading = true;
      fetchGetPerformN9({
        request_type: "n9 perform",
        instance_id: this.instanceValue,
        sequence_no: 3356,
      })
        .then((response) => {
          this.loading = false;
          console.log(response);
          if ((response != null) & (response != [])) {
            this.serverData = response;
            // 拼接组装表格数据
            if (this.serverData.n9aIncPktDnnList) {
              this.serverData.n9aIncPktDnnList.forEach((element, index) => {
                element.n9a_og_pkt = this.serverData.n9aOgPktDnnList[
                  index
                ].n9a_og_pkt;
                element.n9a_inc_oct = this.serverData.n9aIncOctDnnList[
                  index
                ].n9a_inc_oct;
                element.n9a_og_oct = this.serverData.n9aOgOctDnnList[
                  index
                ].n9a_og_oct;
                element.n9a_disc_pkt = this.serverData.n9aDiscPktDnnList[
                  index
                ].n9a_disc_pkt;

                element.n9c_inc_pkt = this.serverData.n9cIncPktDnnList[
                  index
                ].n9c_inc_pkt;
                element.n9c_og_pkt = this.serverData.n9cOgPktDnnList[
                  index
                ].n9c_og_pkt;
                element.n9c_inc_oct = this.serverData.n9cIncOctDnnList[
                  index
                ].n9c_inc_oct;
                element.n9c_og_oct = this.serverData.n9cOgOctDnnList[
                  index
                ].n9c_og_oct;
                element.n9c_disc_pkt = this.serverData.n9cDiscPktDnnList[
                  index
                ].n9c_disc_pkt;
              });
            }
          }
        })
        .catch((e) => {
          this.loading = false;
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
  },
};
</script>

